html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #f1f4fd;
  font-family: Verdana, Geneva, Tahoma, sans-serif; }

.site-header {
  box-sizing: border-box;
  left: 0;
  padding: 16px;
  background: #757f9a;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #757f9a, #d7dde8);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #757f9a, #d7dde8);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  opacity: 0.9;
  position: fixed;
  top: 0;
  width: 100%; }

.main {
  margin: 30px auto;
  max-width: 700px;
  padding-top: 52px;
  width: 100%; }

.header__title {
  color: #0e225b;
  font-size: 40px; }

.about__header {
  color: #790629;
  font-size: 20px; }

.about__paragraph {
  border-top: 3px dotted #db2b39;
  color: #0e225b;
  line-height: 24px;
  margin: 0;
  padding: 16px; }

.about__picture,
.article__picture {
  border-radius: 5px;
  display: block;
  max-width: 700px;
  width: 100%; }

.diary__header {
  color: #790629; }

.days {
  padding: 16px; }

.days:first-child {
  background: #f7f052; }

.days:nth-child(4) {
  background: #f7f052; }

.days:last-child {
  background: #f7f052; }

.days__header {
  color: #790629;
  font-size: 20px;
  margin: 0 0 16px; }

.days__header::before {
  background: #ff6663;
  border-radius: 25%;
  content: "";
  display: inline-block;
  height: 10px;
  margin-bottom: 2px;
  margin-right: 8px;
  transform: rotate(45deg);
  width: 10px; }

.days__header::after {
  background: #ff6663;
  border-radius: 25%;
  content: "";
  display: inline-block;
  height: 10px;
  margin-bottom: 2px;
  margin-left: 8px;
  transform: rotate(45deg);
  width: 10px; }

.days__link:hover {
  color: #db2b39; }

.days__paragraph {
  color: #0e225b;
  line-height: 24px; }

span {
  font-weight: bold; }

.navigation__item {
  display: block;
  margin: 0 16px; }

.navigation__link {
  color: #0e225b;
  text-decoration: none; }

.navigation__link:hover {
  color: #fff; }

.navigation__list {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 0; }

.about__link,
.days__link,
.footer__link,
.article__link {
  color: #0e225b;
  font-weight: 600;
  text-decoration: none; }

a:visited {
  color: #0e225b; }

.about__link:hover {
  color: #db2b39; }

.article__link:hover {
  color: #db2b39; }

.article {
  max-width: 700px;
  width: 100%; }

.article__paragraph {
  color: #0e225b;
  line-height: 24px;
  margin: 0;
  padding: 10px 16px 10px 16px; }

.article__title {
  color: #790629;
  font-size: 20px;
  margin: 16px 0 16px; }

.footer {
  display: flex;
  justify-content: center;
  background: #757f9a;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #d7dde8, #757f9a);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #d7dde8, #757f9a);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  opacity: 0.9;
  width: 100%;
  padding: 16px;
  left: 0;
  bottom: 0;
  position: fixed; }

.footer__paragraph {
  color: #0e225b;
  width: 700px; }

.footer__link--top {
  bottom: 15px;
  position: fixed;
  right: 15px;
  padding: 5px; }

.footer__link:hover {
  color: #fff; }

.fas {
  margin-right: 8px; }

.diary__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }

@media (max-width: 800px) {
  .header__title {
    font-size: 34px; }
  .main {
    margin: 30px auto;
    max-width: 600px; }
  .footer__paragraph {
    width: 600px; } }

@media (max-width: 640px) {
  .diary__grid {
    grid-template-columns: 1fr; }
  .header__title {
    font-size: 28px;
    line-height: 40px; }
  .about__header,
  .diary__header,
  .days__header,
  .article__title {
    font-size: 16px; }
  body {
    font-size: 13px; }
  .days:nth-child(3) {
    background: #f7f052; }
  .days:nth-child(4) {
    background: initial; }
  .days:last-child {
    background: #f7f052; }
  .main {
    margin: 20px auto;
    max-width: 500px;
    padding-top: 32px; }
  .navigation__item {
    padding: 16px; }
  .navigation__list {
    flex-direction: column;
    display: none; }
  .navigation__list--visible {
    display: flex; }
  .navigation__switcher {
    border: none;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px; }
  .site-header {
    min-height: 44px; }
  .footer {
    padding: 0; }
  .footer__link--top {
    bottom: 6px;
    position: fixed;
    right: 0px;
    padding: 5px; }
  .footer__paragraph {
    width: 500px; } }

@media (min-width: 641px) {
  .navigation__switcher {
    display: none; } }

@media (max-width: 360px) {
  .header__title {
    font-size: 16px;
    line-height: 26px; }
  .main {
    margin: 20px auto;
    max-width: 280px; }
  .footer {
    padding: 0; }
  .footer__link--top {
    bottom: 6px;
    position: fixed;
    right: 0px;
    padding: 5px; }
  .footer__paragraph {
    max-width: 280px; } }
